import React from "react";
import Balancer from "react-wrap-balancer";
import { useTranslation } from "gatsby-plugin-react-i18next";
import { getDate } from "../../../hooks/useEditionData";
import { Edition } from "../../../data";
import { HackathonDetails } from "../types";
import RegisterButton from "../../elements/buttons/registerButton";

import * as styles from "./hackathon.module.css";

type HackathonProps = {
  edition: Edition;
};

const Hackathon: React.FC<HackathonProps> = ({ edition }) => {
  const { t } = useTranslation();

  const date = getDate(edition, "hackathon");
  const details = t("details", {
    returnObjects: true,
  }) as HackathonDetails[];

  return (
    <div className={`page ${styles.hackathon}`} id={"Hackathon"}>
      <h2 className={"date"}>
        <Balancer>{date}</Balancer>
      </h2>
      <h1 className={"title"}>{t("title")}</h1>

      <div className={styles.info}>
        {details.map((item, index) => (
          <div key={"Hackathon" + index}>
            <p className={"p"}>{t(item.title)}</p>
            <p className={"p"}>{t(item.content)}</p>
          </div>
        ))}
      </div>

      <RegisterButton />
    </div>
  );
};

export default Hackathon;
