import React from "react";
import { graphql, PageProps } from "gatsby";
import { useTranslation } from "gatsby-plugin-react-i18next";

import Layout from "../layout/layout";
import Seo from "../layout/seo/seo";
import Hackathon from "../components/hackathon/pages/hackathon";
import { currentEdition } from "../data";

const HackathonPage: React.FC<PageProps> = ({ location }) => {
  const { t } = useTranslation();

  return (
    <Layout>
      <Seo title={t("title")} pathname={location.pathname} />

      <Hackathon edition={currentEdition} />
    </Layout>
  );
};

export default HackathonPage;

export const query = graphql`
  query ($language: String!) {
    locales: allLocale(
      filter: {
        ns: { in: ["common", "hackathon"] }
        language: { eq: $language }
      }
    ) {
      edges {
        node {
          ns
          data
          language
        }
      }
    }
  }
`;
